/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { atom, selector, useRecoilState, useRecoilValue } from 'recoil'
import { countBy } from 'lodash'
import MuiButton from '@mui/material/Button'

import { MyUser, Metric } from '../../types'
import { AccordionPanel } from '../AccordionPanel'
import { Title } from '../Title'
import { ReadOnlyField } from '../ReadOnlyField'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'
import {
  lookupListToOptions,
  selectCalculations,
  selectMetricGroups,
  selectMyUser,
  selectMetricReports,
  selectUsers,
  useInvalidate,
} from '../../data/selectors'
import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { useUpdatableState } from '../../lib/useUpdatableState'
import { TextField } from '../TextField'
import { AutocompleteField } from '../AutocompleteField'
import { createMetric, deleteMetric, updateMetric } from '../../data/calls'
import { CheckboxField } from '../CheckboxField'
import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { Menu, Divider } from '../Menu'
import { ConfirmationDialog } from '../ConfirmationDialog'
import { ModelsField } from '../ModelsField'
import { AutocompleteArrayField } from '../AutocompleteArrayField'
import { trimAllStrings } from '../../lib/trimAllStrings'

export interface MetricDetailProps {
  metric: Metric | null
}

interface Fields {
  name: string
  abbreviation: string
  groupId: number | null
  displayText: string
  isStandardized: boolean
  isActive: boolean
  description: string
  calculationId: number | null
  reportIds: number[]
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ metric }: Pick<MetricDetailProps, 'metric'>): State => {
  const {
    name = '',
    abbreviation = '',
    groupId = null,
    displayText = '',
    isStandardized = false,
    isActive = false,
    description = '',
    calculationId = null,
    reportIds = [],
  } = metric ?? {}
  return {
    editMode: metric ? 'stopped' : 'started',
    isDirty: false,
    values: {
      name,
      abbreviation,
      groupId,
      displayText,
      isStandardized,
      isActive,
      description,
      calculationId,
      reportIds,
    },
  }
}

export const MetricDetail = ({ metric }: MetricDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = useInvalidate()
  const notifications = useNotifications()
  const navigate = useNavigate()
  const user = useRecoilValue(selectMyUser)

  const isNewMetric = !metric

  // OPTIONS

  const metricGroupOptions = lookupListToOptions(useRecoilValue(selectMetricGroups))
  const calculationOptions = lookupListToOptions(useRecoilValue(selectCalculations))
  const metricReportOptions = lookupListToOptions(useRecoilValue(selectMetricReports))

  // STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ metric }), [metric])
  const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)

  const { values, editMode, isDirty } = state

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {
      name: !values.name.trim(),
      abbreviation: !values.abbreviation.trim(),
      groupId: !values.groupId,
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'
  const isDeleting = editMode === 'deleting'

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }
  const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

  const handleEdit = () => {
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const handleCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const handleSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      const metricInput = trimAllStrings(values)
      if (isNewMetric) {
        // new metric
        const metricId = await createMetric(api, invalidate)(metricInput)
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
        window.setTimeout(() => {
          navigate.metricDetail({ metricId }, true) // let state update
        })
      } else {
        // existing metric
        await updateMetric(
          api,
          invalidate
        )({
          ...metricInput,
          id: metric.id,
        })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      }
    } catch (err) {
      notifications.error(`Failed to save: ${err}`)
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleDelete = async () => {
    try {
      if (!metric) throw new Error('No metric.')
      updateState((draft) => {
        draft.editMode = 'deleting'
      })
      await deleteMetric(api, invalidate)(metric)
      notifications.info(`Deleted metric ${metric.name}`)
      navigate.metrics()
    } catch (err) {
      notifications.error(`Failed to delete: ${err}`)
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }
  }

  // RENDER

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', alignItems: 'center', minHeight: '3rem', marginBottom: '1rem' }}>
          {!isEditing ? (
            <Title>{values.name}</Title>
          ) : (
            <div css={{ display: 'flex', alignItems: 'baseline' }}>
              <TextField
                label="Metric Name"
                value={values.name}
                onChange={(value) => setFieldValue('name', value)}
                error={errors?.name}
                css={{ width: '30em' }}
                InputProps={{ sx: { fontWeight: '700' } }} // roughly match <Title>
              />
            </div>
          )}
          <div css={{ display: 'flex', gap: '0.5em', marginLeft: 'auto' }}>
            {(isStopped || isDeleting) && (
              <Menu
                label="Actions"
                disabled={isDeleting}
                items={[
                  {
                    key: 'edit',
                    label: 'Edit Metric',
                    onClick: handleEdit,
                  },
                  Divider,
                  {
                    key: 'delete',
                    label: 'Delete Metric',
                    onClick: () => {
                      setIsConfirmDeleteOpen(true)
                    },
                  },
                ]}
              />
            )}
            {(isEditing || isSaving) && (
              <>
                {!isNewMetric && (
                  <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                    Cancel
                  </MuiButton>
                )}
                <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                  Save
                </MuiButton>
              </>
            )}
          </div>
        </div>
        <div
          css={{
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: 'auto auto',
            // gridTemplateAreas: `
            //   "id              contactId          map"
            //   "createdDate     isPrivate          map"
            //   ".               modelIds           map"
            //   ".               description        map"
            //   ".               .                  map"
            // `,
          }}
        >
          <ReadOnlyField
            // gridArea="id"
            value={metric?.id ?? ''}
            label="Metric ID"
          />
          <TextField
            // gridArea="abbreviation"
            value={values.abbreviation}
            onChange={makeFieldChangeHandler('abbreviation')}
            label="Abbreviation"
            readOnly={!isEditing}
            error={errors?.abbreviation}
          />
          <TextField
            // gridArea="displayText"
            value={values.displayText}
            onChange={makeFieldChangeHandler('displayText')}
            label="Display Text"
            readOnly={!isEditing}
            error={errors?.displayText}
          />
          <TextField
            // gridArea="description"
            value={values.description}
            onChange={makeFieldChangeHandler('description')}
            label="Description"
            readOnly={!isEditing}
            error={errors?.description}
            multiline
            minRows={2}
          />
          <CheckboxField
            // gridArea="isActive"
            value={values.isActive}
            onChange={makeFieldChangeHandler('isActive')}
            label="Is Active"
            readOnly={!isEditing}
          />
          <CheckboxField
            // gridArea="isStandardized"
            value={values.isStandardized}
            onChange={makeFieldChangeHandler('isStandardized')}
            label="Is Standardized"
            readOnly={!isEditing}
          />
          <AutocompleteField<number>
            // gridArea="groupId"
            label="Metric Group"
            value={values.groupId}
            onChange={makeFieldChangeHandler('groupId')}
            options={metricGroupOptions}
            readOnly={!isEditing}
            error={errors?.groupId}
          />
          <AutocompleteField<number>
            // gridArea="calculationId"
            label="Calculation"
            value={values.calculationId}
            onChange={makeFieldChangeHandler('calculationId')}
            options={calculationOptions}
            readOnly={!isEditing}
            error={errors?.calculationId}
            isClearable
          />
          <AutocompleteArrayField
            // gridArea="reportIds"
            label="Included In the Following Reports"
            value={values.reportIds}
            onChange={makeFieldChangeHandler('reportIds')}
            options={metricReportOptions}
            readOnly={!isEditing}
            error={errors?.reportIds}
          />
        </div>
      </div>
      <ConfirmationDialog
        title="Confirm Delete Metric"
        isOpen={isConfirmDeleteOpen}
        onClose={() => {
          setIsConfirmDeleteOpen(false)
        }}
        onConfirm={handleDelete}
      >
        Are you sure you want to delete this metric?
      </ConfirmationDialog>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
