/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { atom, selector, useRecoilState, useRecoilValue } from 'recoil'
import { countBy } from 'lodash'
import MuiButton from '@mui/material/Button'

import { MyUser, Predictor } from '../../types'
import { AccordionPanel } from '../AccordionPanel'
import { Title } from '../Title'
import { ReadOnlyField } from '../ReadOnlyField'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'
import { selectMyUser, selectUsers, useInvalidate } from '../../data/selectors'
import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { useUpdatableState } from '../../lib/useUpdatableState'
import { TextField } from '../TextField'
import { AutocompleteField } from '../AutocompleteField'
import { createPredictor, deletePredictor, updatePredictor } from '../../data/calls'
import { CheckboxField } from '../CheckboxField'
import { ConfirmNavigateAway, useNavigate } from '../AppRouter'
import { Menu, Divider } from '../Menu'
import { ConfirmationDialog } from '../ConfirmationDialog'
import { ModelsField } from '../ModelsField'

export interface PredictorDetailProps {
  predictor: Predictor | null
}

interface Fields {
  name: string
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping' | 'deleting'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const getInitialState = ({ predictor, user }: Pick<PredictorDetailProps, 'predictor'> & { user: MyUser }): State => ({
  editMode: predictor ? 'stopped' : 'started',
  isDirty: false,
  values: {
    name: predictor?.name ?? '',
  },
})

export const PredictorDetail = ({ predictor }: PredictorDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = useInvalidate()
  const notifications = useNotifications()
  const navigate = useNavigate()
  const user = useRecoilValue(selectMyUser)

  const isNewPredictor = !predictor

  // STATE

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ predictor, user }), [predictor])
  const [isConfirmDeleteOpen, setIsConfirmDeleteOpen] = useState(false)

  const { values, editMode, isDirty } = state

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {
      name: !values.name.trim(),
    }
    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const isStopped = editMode === 'stopped'
  const isEditing = editMode === 'started'
  const isSaving = editMode === 'stopping'
  const isDeleting = editMode === 'deleting'

  // HANDLERS

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }

  const handleEdit = () => {
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const handleCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const handleSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      const predictorInput = {
        name: values.name.trim(),
      }
      if (isNewPredictor) {
        // new predictor
        const predictorId = await createPredictor(api, invalidate)(predictorInput)
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
        window.setTimeout(() => {
          navigate.predictorDetail({ predictorId }, true) // let state update
        })
      } else {
        // existing predictor
        await updatePredictor(
          api,
          invalidate
        )({
          ...predictorInput,
          id: predictor.id,
        })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      }
    } catch (err) {
      if ((err as Error).message === 'duplicate key value violates unique constraint "predictors_predictor_name_key"') {
        notifications.error(`Failed to save: name "${values.name}" already in use`)
      } else {
        notifications.error(`Failed to save: ${err}`)
      }
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  const handleDelete = async () => {
    try {
      if (!predictor) throw new Error('No predictor.')
      updateState((draft) => {
        draft.editMode = 'deleting'
      })
      await deletePredictor(api, invalidate)(predictor)
      notifications.info(`Deleted predictor ${predictor.name}`)
      navigate.predictors()
    } catch (err) {
      notifications.error(`Failed to delete: ${err}`)
      updateState((draft) => {
        draft.editMode = 'stopped'
      })
    }
  }

  // RENDER

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', alignItems: 'center', minHeight: '3rem', marginBottom: '1rem' }}>
          {!isEditing ? (
            <Title>Predictor {values.name}</Title>
          ) : (
            <div css={{ display: 'flex', alignItems: 'baseline' }}>
              <Title>Predictor&nbsp;</Title>
              <TextField
                value={values.name}
                onChange={(value) => setFieldValue('name', value)}
                error={errors?.name}
                css={{ width: '30em' }}
                InputProps={{ sx: { fontWeight: '700' } }} // roughly match <Title>
              />
            </div>
          )}
          <div css={{ display: 'flex', gap: '0.5em', marginLeft: 'auto' }}>
            {(isStopped || isDeleting) && (
              <Menu
                label="Actions"
                disabled={isDeleting}
                items={[
                  {
                    key: 'edit',
                    label: 'Edit Predictor',
                    onClick: handleEdit,
                  },
                  Divider,
                  {
                    key: 'delete',
                    label: 'Delete Predictor',
                    onClick: () => {
                      setIsConfirmDeleteOpen(true)
                    },
                  },
                ]}
              />
            )}
            {(isEditing || isSaving) && (
              <>
                {!isNewPredictor && (
                  <MuiButton onClick={handleCancel} variant="outlined" disabled={isSaving}>
                    Cancel
                  </MuiButton>
                )}
                <MuiButton onClick={handleSave} variant="contained" disabled={!isDirty || isSaving || !!errors}>
                  Save
                </MuiButton>
              </>
            )}
          </div>
        </div>
        <div
          css={{
            flex: 1,
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: 'auto 1fr auto',
            gridTemplateAreas: `
              "id              contactId          map"
              "createdDate     isPrivate          map"
              ".               modelIds           map"
              ".               description        map"
              ".               .                  map"
            `,
          }}
        >
          <ReadOnlyField gridArea="id" value={predictor?.id ?? ''} label="Predictor ID" />
          {/* <ReadOnlyField
              gridArea="createdDate"
              value={formatUtcDateTimeToAppDate(predictor?.createdDate ?? null)}
              label="Created On"
            />
            <AutocompleteField
              gridArea="contactId"
              value={values.contactId}
              onChange={(value) => setFieldValue('contactId', value)}
              valueLabel={predictor?.contactName ?? ''}
              optionsSelector={selectUserOptions}
              label="Contact Name"
              readOnly={!isEditing}
            />
            <CheckboxField
              gridArea="isPrivate"
              value={!values.isPrivate}
              onChange={(value) => setFieldValue('isPrivate', !value)}
              label="Predictor samples appear in the public web map"
              readOnly={!isEditing}
            />
            <ModelsField
              gridArea="modelIds"
              value={values.modelIds}
              onChange={(value) => setFieldValue('modelIds', value)}
              label="Models"
              readOnly={!isEditing}
            />
            <TextField
              gridArea="description"
              value={values.description}
              onChange={(value) => setFieldValue('description', value)}
              label="Description"
              readOnly={!isEditing}
              multiline
              minRows={3}
            /> */}
        </div>
      </div>
      <ConfirmationDialog
        title="Confirm Delete Predictor"
        isOpen={isConfirmDeleteOpen}
        onClose={() => {
          setIsConfirmDeleteOpen(false)
        }}
        onConfirm={handleDelete}
      >
        Are you sure you want to delete this predictor?
      </ConfirmationDialog>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
